<script>
    import { pb } from '../services/pocketbase';
    import { createEventDispatcher } from 'svelte';
  
    export let contract;
    export let isAdmin;
  
    let expanded = false;
    let file;
    let uploadStatus = '';
    let userRole = pb.authStore.model?.role || '';
  
    const dispatch = createEventDispatcher();
  
    console.log('ContractCard: isAdmin =', isAdmin);
    console.log('ContractCard: userRole =', userRole);
  
    function toggleExpand() {
      expanded = !expanded;
    }
  
    async function uploadFile() {
      if (!file) {
        uploadStatus = 'Please select a file first.';
        return;
      }
  
      uploadStatus = 'Uploading...';
      console.log('Attempting to upload file:', file[0].name);
  
      try {
        const formData = new FormData();
        formData.append('data', file[0]);
        formData.append('contract', contract.id);
        formData.append('owner', pb.authStore.model.id);
  
        console.log('FormData:', formData);
  
        const createdDataPoint = await pb.collection('DataPoint').create(formData);
        
        console.log('DataPoint created:', createdDataPoint);
        
        uploadStatus = 'File uploaded successfully!';
        dispatch('fileUploaded', { dataPoint: createdDataPoint });
        
        file = null;
      } catch (error) {
        console.error('Error uploading file:', error);
        uploadStatus = `Error uploading file: ${error.message}`;
      }
    }
  
    function handleKeyDown(event) {
      if (event.key === 'Enter' || event.key === ' ') {
        toggleExpand();
      }
    }
  
    function getThumbnailUrl(contract) {
      if (contract.thumbnail) {
        return pb.files.getUrl(contract, contract.thumbnail);
      }
      return 'path/to/default/thumbnail.jpg';
    }
  
    $: canUpload = isAdmin || userRole === 'DataProvider';
    $: console.log('canUpload:', canUpload);
  </script>
  
  <div 
    class="bg-white shadow-md rounded-lg p-4 cursor-pointer" 
    on:click={toggleExpand}
    on:keydown={handleKeyDown}
    tabindex="0"
    role="button"
    aria-expanded={expanded}
  >
    <div class="flex items-start">
      <img 
        src={getThumbnailUrl(contract)} 
        alt="Contract thumbnail" 
        class="w-20 h-20 object-cover rounded mr-4"
      />
      <div>
        
        <h2 class="text-xl font-semibold mb-2">{contract.title}</h2>
        <p class="text-gray-600">{contract.status}</p>
      </div>
    </div>
    
    {#if expanded}
      <div class="mt-4">
        {@html contract.description}
        <p>Deadline: {new Date(contract.deadline).toLocaleDateString()}</p>
        <p>Amount Requested: {contract.amount_requested}</p>
        <p>Price Per Point: {contract.price_per_point}</p>
        
        <div class="mt-4">
          {#if canUpload}
            <input type="file" bind:files={file} accept=".csv,.json,.txt,.image/*">
            <button 
              on:click|stopPropagation={uploadFile} 
              class="mt-2 bg-blue-500 text-white px-4 py-2 rounded hover:bg-blue-600"
            >
              Upload File
            </button>
            {#if uploadStatus}
              <p class={uploadStatus.includes('Error') ? 'text-red-500' : 'text-green-500'}>
                {uploadStatus}
              </p>
            {/if}
          {:else}
            <p>You don't have permission to upload files to this contract.</p>
          {/if}
        </div>
      </div>
    {/if}
  </div>