<script>
	import { Router, Link, Route } from "svelte-routing";
	import { pb } from './services/pocketbase';
	import Home from "./routes/Home.svelte";
	import Contracts from "./routes/Contracts.svelte";
	import Login from "./routes/Login.svelte";
	import NewContract from "./routes/NewContract.svelte";
	import Header from "./components/Header.svelte";
	import Footer from "./components/Footer.svelte";
	import Terms from "./routes/Terms.svelte";
	import Privacy from "./routes/Privacy.svelte";
	import CoolCard from './components/CoolCard.svelte';

	// Import icons
	import { FaHome, FaFileContract, FaPlusCircle, FaSignOutAlt } from 'svelte-icons/fa';
	
	export let url = "";
	let isAuthenticated = pb.authStore.isValid;
	let userRole = pb.authStore.model?.role || '';
  
	pb.authStore.onChange(() => {
	  isAuthenticated = pb.authStore.isValid;
	  userRole = pb.authStore.model?.role || '';
	});
  
	function logout() {
	  pb.authStore.clear();
	}
  </script>
  
  <Router {url}>
	{#if isAuthenticated}
	  <div class="flex flex-col min-h-screen">
		<Header />
		<div class="flex flex-grow">
		  <nav class="w-64 bg-gray-100 p-4">
			<ul class="space-y-4">
			  <li class="flex items-center p-2 rounded hover:bg-gray-200">
				<div class="w-5 h-5 mr-2">
				  <FaHome />
				</div>
				<Link to="/" class="block hover:text-blue-600">Home</Link>
			  </li>
			  <li class="flex items-center p-2 rounded hover:bg-gray-200">
				<div class="w-5 h-5 mr-2">
				  <FaFileContract />
				</div>
				<Link to="/contracts" class="block hover:text-blue-600">Contracts</Link>
			  </li>
			  {#if userRole === 'Customer'}
				<li class="flex items-center p-2 rounded hover:bg-gray-200">
				  <div class="w-5 h-5 mr-2">
					<FaPlusCircle />
				  </div>
				  <Link to="/new-contract" class="block hover:text-blue-600">New Contract</Link>
				</li>
			  {/if}
			</ul>
		  </nav>
		  <main class="flex-grow p-4 bg-gray-50">
			<Route path="/" component={Home} />
			<Route path="/contracts" component={Contracts} />
			{#if userRole === 'Customer'}
			  <Route path="/new-contract" component={NewContract} />
			{/if}
			<Route path="/terms" component={Terms} />
			<Route path="/privacy" component={Privacy} />
		  </main>
		</div>
		<Footer />
	  </div>
	{:else}
	  <Route path="*" component={Login} />
	{/if}
  </Router>
  
  <style>
	:global(body) {
	  margin: 0;
	  padding: 0;
	}
	:global(svg) {
	  width: 100%;
	  height: 100%;
	}
  </style>